.report-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f4f6f9, #e3ebf6);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
}

.report-card {
  width: 100%;
  max-width: 800px;
  background: #ffffff;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.report-header {
  text-align: center;
  margin-bottom: 25px;
}

.report-header h2 {
  margin-bottom: 5px;
  color: #333;
}

.report-header p {
  color: #777;
  font-size: 14px;
}

.score-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.score-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: conic-gradient(#4a6cf7 78%, #e6e6e6 0%);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
}

.score-circle span {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.score-details p {
  margin: 5px 0;
  font-size: 14px;
  color: #444;
}

.report-section {
  margin-bottom: 20px;
}

.report-section h3 {
  margin-bottom: 10px;
  color: #4a6cf7;
}

.report-section ul {
  list-style: none;
  padding-left: 0;
}

.report-section li {
  margin-bottom: 8px;
  font-size: 14px;
  color: #333;
}

.report-actions {
  margin-top: 20px;
  text-align: center;
}

.download-btn,
.retry-btn {
  padding: 10px 20px;
  margin: 5px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: 0.3s ease;
}

.download-btn {
  background-color: #4a6cf7;
  color: white;
}

.retry-btn {
  background-color: #28a745;
  color: white;
}

.download-btn:hover {
  background-color: #3554d1;
}

.retry-btn:hover {
  background-color: #1f8a38;
}

/* Responsive */
@media (max-width: 768px) {
  .score-section {
    flex-direction: column;
    align-items: center;
  }

  .score-circle {
    margin-bottom: 20px;
  }
}
