.profile-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f4f6f9, #e3ebf6);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
}

.profile-card {
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 30px;
  transition: 0.3s ease;
}

.profile-header {
  text-align: center;
  margin-bottom: 25px;
}

.profile-avatar {
  width: 80px;
  height: 80px;
  background: #4a6cf7;
  color: white;
  font-size: 32px;
  font-weight: bold;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 10px auto;
}

.profile-header h2 {
  margin: 5px 0;
  color: #333;
}

.profile-header p {
  color: #777;
  font-size: 14px;
}

.profile-details {
  margin-top: 20px;
}

.profile-field {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.profile-field label {
  font-size: 13px;
  font-weight: 600;
  margin-bottom: 5px;
  color: #555;
}

.profile-field span {
  padding: 8px 0;
  color: #333;
}

.profile-field input {
  padding: 8px 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 14px;
  transition: 0.2s ease;
}

.profile-field input:focus {
  border-color: #4a6cf7;
}

.profile-actions {
  margin-top: 20px;
  text-align: center;
}

.edit-btn,
.save-btn {
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: 0.3s ease;
}

.edit-btn {
  background-color: #4a6cf7;
  color: white;
}

.save-btn {
  background-color: #28a745;
  color: white;
}

.edit-btn:hover {
  background-color: #3554d1;
}

.save-btn:hover {
  background-color: #1f8a38;
}

/* Responsive */
@media (max-width: 768px) {
  .profile-card {
    padding: 20px;
  }

  .profile-avatar {
    width: 65px;
    height: 65px;
    font-size: 24px;
  }
}
