/* ===========================
   Progress Chart Container
=========================== */

.progress-container {
  position: relative;
  width: 160px;
  height: 160px;
  margin: 20px auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ===========================
   SVG Rotation (Start from top)
=========================== */

.progress-container svg {
  transform: rotate(-90deg);
}

/* ===========================
   Progress Circle Animation
=========================== */

.progress-circle {
  transition: stroke-dashoffset 1s ease-in-out;
  filter: drop-shadow(0 0 6px rgba(59, 130, 246, 0.4));
}

/* ===========================
   Center Text
=========================== */

.progress-text {
  position: absolute;
  text-align: center;
  transform: rotate(90deg);
}

.progress-text h2 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
  color: #1e293b;
}

.progress-text p {
  font-size: 13px;
  margin-top: 4px;
  color: #64748b;
}

/* ===========================
   Responsive Design
=========================== */

@media (max-width: 768px) {
  .progress-container {
    width: 140px;
    height: 140px;
  }

  .progress-text h2 {
    font-size: 20px;
  }

  .progress-text p {
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .progress-container {
    width: 120px;
    height: 120px;
  }

  .progress-text h2 {
    font-size: 18px;
  }

  .progress-text p {
    font-size: 11px;
  }
}

/* ===========================
   Dark Mode Support
   (Works if body has class 'dark')
=========================== */

body.dark .progress-text h2 {
  color: #f1f5f9;
}

body.dark .progress-text p {
  color: #94a3b8;
}

body.dark .progress-circle {
  filter: drop-shadow(0 0 8px rgba(56, 189, 248, 0.6));
}
