.settings-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f4f6f9, #e3ebf6);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  transition: 0.3s ease;
}

.settings-card {
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.settings-card h2 {
  text-align: center;
  margin-bottom: 20px;
}

.setting-section {
  margin-bottom: 20px;
}

.setting-section h3 {
  margin-bottom: 10px;
  color: #4a6cf7;
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.setting-section input,
.setting-section select {
  width: 100%;
  padding: 8px 10px;
  margin-top: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 14px;
}

.setting-section input:focus,
.setting-section select:focus {
  border-color: #4a6cf7;
}

.settings-actions {
  text-align: center;
  margin-top: 20px;
}

.settings-actions button {
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  background-color: #4a6cf7;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.settings-actions button:hover {
  background-color: #3554d1;
}

/* Dark Mode */
.dark {
  background: linear-gradient(135deg, #1e1e2f, #2a2a40);
}

.dark .settings-card {
  background: #2f2f45;
  color: white;
}

.dark input,
.dark select {
  background: #444;
  color: white;
  border: 1px solid #666;
}

/* Responsive */
@media (max-width: 768px) {
  .settings-card {
    padding: 20px;
  }
}
