.dashboard-container {
  padding: 60px 40px;
  background: #f5f7fb;
  min-height: 100vh;
  font-family: "Segoe UI", sans-serif;
}

/* ===== HEADER ===== */
.dashboard-header {
  margin-bottom: 40px;
}

.dashboard-header h1 {
  font-size: 2.2rem;
  color: #1e3c72;
}

.dashboard-header p {
  color: #555;
  margin-top: 5px;
}

/* ===== STATS GRID ===== */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 25px;
  margin-bottom: 50px;
}

.stat-card {
  background: white;
  padding: 30px;
  border-radius: 18px;
  text-align: center;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.06);
  transition: 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-6px);
}

.stat-card h2 {
  font-size: 2rem;
  color: #ff9800;
  margin-bottom: 8px;
}

.stat-card p {
  color: #555;
  font-size: 0.9rem;
}

/* ===== QUICK ACTIONS ===== */
.quick-actions {
  margin-bottom: 50px;
}

.quick-actions h2 {
  margin-bottom: 20px;
  color: #2a5298;
}

.action-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.action-btn {
  padding: 12px 25px;
  border-radius: 25px;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  color: white;
  text-decoration: none;
  font-weight: 600;
  transition: 0.3s ease;
}

.action-btn:hover {
  transform: scale(1.05);
}

.action-btn.secondary {
  background: #ff9800;
}

.action-btn.secondary:hover {
  background: #e68900;
}

/* ===== RECENT ACTIVITY ===== */
.recent-activity h2 {
  margin-bottom: 15px;
  color: #2a5298;
}

.recent-activity ul {
  background: white;
  padding: 25px;
  border-radius: 18px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.06);
  list-style: none;
}

.recent-activity li {
  margin-bottom: 10px;
  font-size: 0.95rem;
  color: #555;
}

/* ===== RESPONSIVE ===== */
@media (max-width: 768px) {
  .dashboard-container {
    padding: 40px 20px;
  }

  .dashboard-header h1 {
    font-size: 1.7rem;
  }

  .action-buttons {
    flex-direction: column;
  }
}
