.result-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  padding: 20px;
}

.result-card {
  background: white;
  width: 100%;
  max-width: 450px;
  padding: 40px 30px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.4s ease-in-out;
}

.result-card h1 {
  color: #1e3c72;
  margin-bottom: 10px;
}

.domain-name {
  font-size: 0.9rem;
  color: #555;
  margin-bottom: 25px;
}

/* ===== SCORE CIRCLE ===== */
.score-circle {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ff9800, #ffb74d);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 25px;
  color: white;
  font-size: 1.8rem;
  font-weight: bold;
}

/* ===== SCORE DETAILS ===== */
.score-details {
  font-size: 0.95rem;
  color: #444;
  margin-bottom: 20px;
}

/* ===== PERFORMANCE BADGE ===== */
.performance-badge {
  padding: 10px 15px;
  border-radius: 25px;
  font-weight: bold;
  margin-bottom: 25px;
}

.performance-badge.excellent {
  background: #4caf50;
  color: white;
}

.performance-badge.good {
  background: #ff9800;
  color: white;
}

.performance-badge.poor {
  background: #f44336;
  color: white;
}

/* ===== BUTTONS ===== */
.result-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.result-buttons button {
  padding: 10px 20px;
  border-radius: 25px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  background: #1e3c72;
  color: white;
  transition: 0.3s ease;
}

.result-buttons button:hover {
  transform: scale(1.05);
}

.result-buttons .secondary {
  background: #ff9800;
}

.result-buttons .secondary:hover {
  background: #e68900;
}

/* ===== ANIMATION ===== */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== RESPONSIVE ===== */
@media (max-width: 500px) {
  .result-card {
    padding: 30px 20px;
  }

  .score-circle {
    width: 110px;
    height: 110px;
    font-size: 1.5rem;
  }
}
