/* ===== Global Home Wrapper ===== */
.home {
  width: 100%;
  overflow-x: hidden;
  font-family: "Segoe UI", sans-serif;
}

/* ===== HERO SECTION ===== */
.hero {
  min-height: 90vh;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 40px 20px;
  color: white;
}

.hero-content {
  max-width: 700px;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.hero p {
  font-size: 1.1rem;
  margin-bottom: 30px;
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.btn {
  padding: 12px 25px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 600;
  transition: 0.3s ease;
  display: inline-block;
}

.primary {
  background: #ff9800;
  color: white;
}

.primary:hover {
  background: #e68900;
}

.secondary {
  background: white;
  color: #2a5298;
}

.secondary:hover {
  background: #f0f0f0;
}

/* ===== FEATURES SECTION ===== */
.features {
  padding: 70px 20px;
  text-align: center;
  background: #f9f9f9;
}

.features h2 {
  margin-bottom: 40px;
  font-size: 2rem;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}

.feature-card {
  background: white;
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.08);
  transition: 0.3s;
}

.feature-card:hover {
  transform: translateY(-5px);
}

/* ===== DOMAINS SECTION ===== */
.domains {
  padding: 70px 20px;
  text-align: center;
}

.domains h2 {
  margin-bottom: 30px;
}

.domain-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.domain-card {
  background: linear-gradient(135deg, #2a5298, #1e3c72);
  color: white;
  padding: 20px;
  border-radius: 12px;
  font-weight: 600;
  transition: 0.3s;
  cursor: pointer;
}

.domain-card:hover {
  transform: scale(1.05);
}

/* ===== CTA SECTION ===== */
.cta {
  background: #1e3c72;
  color: white;
  padding: 60px 20px;
  text-align: center;
}

.cta h2 {
  margin-bottom: 25px;
}

/* ===== RESPONSIVE DESIGN ===== */

/* Tablet */
@media (max-width: 992px) {
  .hero h1 {
    font-size: 2.3rem;
  }

  .hero p {
    font-size: 1rem;
  }
}

/* Mobile */
@media (max-width: 600px) {
  .hero {
    min-height: 75vh;
    padding: 20px;
  }

  .hero h1 {
    font-size: 1.8rem;
  }

  .hero p {
    font-size: 0.95rem;
  }

  .btn {
    width: 100%;
    text-align: center;
  }

  .feature-grid {
    grid-template-columns: 1fr;
  }

  .domain-list {
    grid-template-columns: 1fr;
  }
}
