/* ===============================
   Footer Main Styling
================================= */

.footer {
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: #f1f5f9;
  padding: 60px 20px 20px;
  margin-top: 60px;
  font-family: "Segoe UI", sans-serif;
}

.footer-container {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

/* ===============================
   Sections
================================= */

.footer-section h2.footer-logo {
  font-size: 24px;
  margin-bottom: 15px;
  color: #38bdf8;
}

.footer-section h3 {
  font-size: 18px;
  margin-bottom: 15px;
  color: #ffffff;
}

.footer-section p {
  font-size: 14px;
  line-height: 1.6;
  color: #cbd5e1;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 10px;
}

.footer-section ul li a {
  text-decoration: none;
  color: #cbd5e1;
  font-size: 14px;
  transition: all 0.3s ease;
}

.footer-section ul li a:hover {
  color: #38bdf8;
  padding-left: 5px;
}

/* ===============================
   Bottom Section
================================= */

.footer-bottom {
  text-align: center;
  border-top: 1px solid #334155;
  margin-top: 40px;
  padding-top: 15px;
  font-size: 13px;
  color: #94a3b8;
}

/* ===============================
   Responsive Design
================================= */

@media (max-width: 768px) {
  .footer {
    padding: 40px 20px 15px;
  }

  .footer-section h2.footer-logo {
    font-size: 20px;
  }

  .footer-section h3 {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .footer-container {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .footer-section ul li a:hover {
    padding-left: 0;
  }
}
