/* ===== MAIN CONTAINER ===== */
.courses-container {
  width: 100%;
  min-height: 100vh;
  padding: 60px 40px;
  background: #f5f7fb;
  font-family: "Segoe UI", sans-serif;
}

/* ===== HEADER ===== */
.courses-header {
  text-align: center;
  margin-bottom: 50px;
}

.courses-header h1 {
  font-size: 2.5rem;
  color: #1e3c72;
  margin-bottom: 10px;
}

.courses-header p {
  font-size: 1rem;
  color: #555;
}

/* ===== SECTION ===== */
.course-section,
.gov-section {
  margin-bottom: 60px;
}

.course-section h2,
.gov-section h2 {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 35px;
  color: #2a5298;
}

/* ===== GRID LAYOUT ===== */
.course-grid,
.gov-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 25px;
}

/* ===== COURSE CARD ===== */
.course-card,
.gov-card {
  background: white;
  padding: 25px;
  border-radius: 18px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.06);
  transition: 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.course-card:hover,
.gov-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.1);
}

.course-card h3,
.gov-card h3 {
  color: #1e3c72;
  margin-bottom: 10px;
}

.course-card p,
.gov-card p {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 15px;
  line-height: 1.5;
}

/* ===== LEVEL BADGE ===== */
.level-badge {
  background: #ff9800;
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  display: inline-block;
  margin-bottom: 15px;
  width: fit-content;
}

/* ===== BUTTON ===== */
.enroll-btn {
  padding: 10px;
  border: none;
  border-radius: 25px;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.enroll-btn:hover {
  background: linear-gradient(135deg, #2a5298, #1e3c72);
  transform: scale(1.05);
}

/* ===== RESPONSIVE ===== */
@media (max-width: 768px) {
  .courses-container {
    padding: 40px 20px;
  }

  .courses-header h1 {
    font-size: 2rem;
  }

  .course-section h2,
  .gov-section h2 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .courses-header h1 {
    font-size: 1.6rem;
  }

  .courses-header p {
    font-size: 0.9rem;
  }
}
