/* ===== FULL PAGE CONTAINER ===== */
.signup-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  padding: 20px;
}

/* ===== SIGNUP CARD ===== */
.signup-card {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(14px);
  -webkit-backdrop-filter: blur(14px);
  border-radius: 20px;
  padding: 40px 35px;
  width: 100%;
  max-width: 450px;
  color: white;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.25);
  animation: fadeIn 0.5s ease-in-out;
}

/* ===== HEADER ===== */
.signup-card h2 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 2rem;
}

.signup-card p {
  text-align: center;
  font-size: 0.95rem;
  margin-bottom: 25px;
  opacity: 0.9;
}

/* ===== INPUT GROUP ===== */
.input-group {
  margin-bottom: 18px;
  display: flex;
  flex-direction: column;
}

.input-group label {
  margin-bottom: 6px;
  font-size: 0.9rem;
  font-weight: 500;
}

.input-group input {
  padding: 12px;
  border-radius: 10px;
  border: none;
  outline: none;
  font-size: 0.95rem;
  transition: 0.3s ease;
}

/* Focus effect */
.input-group input:focus {
  box-shadow: 0 0 0 2px #ff9800;
}

/* ===== PASSWORD WRAPPER ===== */
.password-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-wrapper input {
  width: 100%;
}

.toggle-password {
  position: absolute;
  right: 12px;
  font-size: 0.85rem;
  cursor: pointer;
  color: #1e3c72;
  font-weight: 600;
}

/* ===== ERROR MESSAGE ===== */
.error {
  font-size: 0.8rem;
  color: #ffd6d6;
  margin-top: 4px;
}

/* ===== SIGNUP BUTTON ===== */
.signup-btn {
  width: 100%;
  padding: 12px;
  border-radius: 25px;
  border: none;
  background: #ff9800;
  color: white;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s ease;
  margin-top: 10px;
}

.signup-btn:hover {
  background: #e68900;
  transform: scale(1.03);
}

.signup-btn:disabled {
  background: #cccccc;
  cursor: not-allowed;
  transform: none;
}

/* ===== EXTRA LINKS ===== */
.extra-links {
  margin-top: 20px;
  text-align: center;
  font-size: 0.9rem;
}

.extra-links a {
  color: #ff9800;
  text-decoration: none;
  font-weight: 600;
}

.extra-links a:hover {
  text-decoration: underline;
}

/* ===== ANIMATION ===== */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== RESPONSIVE ===== */
@media (max-width: 500px) {
  .signup-card {
    padding: 30px 20px;
  }

  .signup-card h2 {
    font-size: 1.6rem;
  }

  .signup-card p {
    font-size: 0.85rem;
  }
}
